input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.app_heading {
	font-size: 2rem;
	color: #171815;
}

body {
	/* background-color: rgb(180, 175, 175) !important; */
	/* background-image: linear-gradient(to right, rgb(104, 204, 243), #aac737;); */
}

span {
	color: red;
	font-size: 14px;
}

.app {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	height: 100vh;
	/* margin: 20px 0px; */
}

.form-container {
	width: 500px;
	margin: 10px 30px;
	padding: 50px 30px;
	border: 1px solid #000;
	box-shadow: -2px 10px 15px 10px rgba(0, 0, 0, 0.44);
}

@media only screen and (max-width: 720px) {
	.form-container {
		width: 400px !important;
	}
}

.header_image {
	width: 250px;
}

form input {
	text-align: left;
	margin: 7px 0px;
}

input {
	padding: 0.375rem 1.5rem;
	text-align: left;
	font-size: 1rem;
	line-height: 1.5;
	background-color: #fff;
	background-clip: padding-box;
	border: 1px solid #16181a;
	border-radius: 0.5rem;
	transition: border-color 0.15s;
}

input::placeholder {
	color: rgb(51, 49, 49);
}

.submit_button {
	background-color: #000;
	color: #fff;
	font-size: 1.1em;
	box-shadow: 7px 5px rgb(155, 152, 152);
	padding: 7px 30px;
	border-radius: 8px;
}

.submit_button:hover {
	background-color: #fff;
	color: #000;
}
